export default {
	data() {
		return {
			detail: {},
			id: '',
		}
	},
	async onLoad(options) {
		this.id = options.id;
		let res = await this.$api.info('news', options.id)
		this.detail = res.data;
		this.detail.content = this.detail.content.replace(/<img/g,'<img style="width: 100%;"');
	}
}
